/* Portofolio Effect */
*,
*:after,
*::before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.zp-grid {
  position: relative;
  margin: 0 auto 20px;
  padding: 0;
  list-style: none;
}

/* Portfolio Archive page 
 ------------------------------------------------------------ */
.post-type-archive-portfolio .zp-grid {
  margin: 0 auto 8px;
}

.zp-grid:before,
.zp-grid:after {
  display: table;
  content: ' ';
}

.zp-grid:after {
  clear: both;
}

.zp-grid div {
  position: relative;
  display: inline-block;
  margin: 10px;
  -webkit-backface-visibility: hidden;
  -moz-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  backface-visibility: hidden;
}

.zp-grid div a,
.zp-grid div a img {
  display: block;
  margin: 0 auto;
  border: none;
  width: 100%;
}

.zp-grid div a {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.zp-grid div a:nth-child(2) {
  z-index: 1;
  visibility: hidden;
}

/* Navigation */
.zp-grid-wrapper nav {
  text-align: center;
}

.zp-grid-wrapper nav a {
  display: inline-block;
  margin: 0 8px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #eee;
  cursor: pointer;
  opacity: 0.7;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.touch .zp-grid-wrapper nav a {
  width: 30px;
  height: 30px;
}

.zp-grid-wrapper nav a:hover {
  opacity: 1;
}

.zp-grid-wrapper nav a.zp-current {
  opacity: 1;
  box-shadow: 0 0 0 5px rgba(255, 255, 255, 0.4);
}

.zp-grid.zp-effect-active div a {
  pointer-events: none;
}

/*
*	Media Queries
*/
@media only screen and (min-width: 40.0625em) {
  .portfolio_title {
    border: 2px solid #0c0c0c;
    color: #fff;
    background-color: transparent;
    border-color: #fff;
  }

  .portfolio_archive_element {
    margin-bottom: 0;
  }

  .portfolio_overlay {
    display: inline-block;
    float: left;
    position: absolute;
    /*background: rgba(81, 94, 108, 0.9);*/
    background: rgba(214, 81, 41, 0.8);
    height: 100%;
    width: 100%;
    z-index: 9999;
    top: -100%;
    display: none;
  }

  .zp-grid-wrapper .element a:hover h4, .zp-grid-wrapper .portfolio_archive_element a:hover h4 {
    top: 45%;
    -webkit-transition: all 0.2s;
    -moz-transition: all 0.2s;
    transition: all 0.2s;
  }

  .zp-grid-wrapper .element a:hover .portfolio_overlay, .zp-grid-wrapper .portfolio_archive_element a:hover .portfolio_overlay {
    top: 0;
    display: block;
  }

  .related_container .element a:hover .portfolio_overlay {
    top: 0;
    display: block;
  }

  .related_container .element a:hover h4 {
