  max-height: 300px;
}

.flexslider .slides {
  zoom: 1;
}

.carousel li {
  margin-right: 5px;
}

/* Nav Dots */
.flex-control-nav {
  text-align: center;
  position: absolute;
  bottom: -40px;
  height: 30px;
  width: 100%;
  left: 0;
  z-index: 999;
  pointer-events: none;
}

.flex-control-nav li {
  display: inline-block;
  position: relative;
  width: 16px;
  height: 16px;
  margin: 8px;
  pointer-events: auto;
}

.flex-control-nav li a {
  display: block;
  cursor: pointer;
  text-indent: -9999em;
  background: #ddd;
  background: #131212;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  box-shadow: 0 1px 1px rgba(255, 255, 255, 0.4), inset 0 1px 1px rgba(0, 0, 0, 0.1), 0 0 0 2px rgba(255, 255, 255, 0.5);
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -ms-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.flex-control-nav li a.flex-active, .flex-control-nav li a:hover {
  box-shadow: 0 1px 1px rgba(255, 255, 255, 0.4), inset 0 1px 1px rgba(0, 0, 0, 0.1), 0 0 0 5px rgba(255, 255, 255, 0.5);
}

.flex-control-nav li a.flex-active:after {
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  top: 3px;
  left: 3px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.8);
}

/* Direction Nav
------------------------------------------------------------ */
.flex-direction-nav {
  height: 0;
  padding: 0 !important;
}

.flex-direction-nav li a {
  cursor: pointer;
  display: block;
  height: 85px;
  position: absolute;
  top: 45%;
  width: 44px;
}

.flex-direction-nav li .flex-next {
  right: 50px;
  color: #fff;
  font-size: 0px;
  text-decoration: none;
}

