  display: initial;
}

.cat-port .galereya-cell-desc {
  background-color: rgba(214, 81, 41, 0.8);
  padding: 0 10px;
  top: 0;
  height: 100%;
  width: 100%;
  text-align: center;
}

.galereya-cell-desc-title {
  position: relative;
  top: 40%;
}

.galereya-cell-desc-title h1 {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  font-size: 35px;
}

.galereya-slider-desc-title h1 {
  font-size: 13.4px;
  color: #fff;
  line-height: 21.7px;
  margin: 0;
  padding: 0;
}

.galereya-cell-desc-text {
  display: none;
}

.galereya.cat-port .galereya-cell-overlay {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.galereya-slider.cat-port {
  opacity: 0;
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
  -ms-transform: translate(0);
  -o-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition: opacity .5s ease-out;
  -moz-transition: opacity .5s ease-out;
  transition: opacity .5s ease-out;
  -o-transition: none;
}

.galereya-slider.cat-port.opened {
  opacity: 100;
}

