.galereya-slider.cat-port .galereya-slider-play,
.galereya-slider.cat-port .galereya-slider-close {
    top: 0;
}
.galereya-slider-close{
	left:auto;
	right:0;
	&:hover{
		opacity:1;
	}
}
.galereya-slider-play{
	left:0;
	right:auto;
	&:hover{
		opacity:1;
	}
}
.galereya-slider-desc{
    margin: 0 48px;
}
.galereya-slider.cat-port .galereya-slider-slide {
    opacity: 100;
    /*to add a background image to the slider itself
    background: #000 url(/wp-content/uploads/2015/06/5th-ave-victorville-ca-batroom-remodel-bathtub.jpg);*/
    background-color: #000;
    -webkit-transition: opacity .5s ease-out;
    -moz-transition: opacity .5s ease-out;
    transition: opacity .5s ease-out;
    -o-transition: none;
}
.galereya-slider.cat-port .galereya-slider-slide.next {
    opacity: 0;
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translate(0);
    -o-transform: translateX(0);
    transform: translateX(0);
}
.galereya-slider.cat-port .galereya-slider-slide.current {}
.galereya-slider.cat-port .galereya-slider-slide.prev {}


    .galereya-slider.cat-port .galereya-slide-img {
        -webkit-box-shadow: 0 0 1em #000;
        box-shadow: 0 0 2em #000;
    }
